@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'Titillium Web';
}


body {
  background: rgb(245, 121, 64);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(100%, rgb(240, 25, 50)), color-stop(2%, rgba(241, 183, 107, 0.88)));
  background: linear-gradient(180deg, rgb(240, 25, 50) 100%, rgba(241, 183, 107, 0.88) 2%);
}

body.workout {
  background: blue;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(34, 8, 131, 0.88)), to(rgb(56, 162, 207)));
  background: linear-gradient(180deg, rgba(34, 8, 131, 0.88) 0%, rgb(56, 162, 207) 100%);
}

body.restout {
  background: rgb(245, 121, 64);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(100%, rgb(240, 25, 50)), color-stop(2%, rgba(241, 183, 107, 0.88)));
  background: linear-gradient(180deg, rgb(240, 25, 50) 100%, rgba(241, 183, 107, 0.88) 2%);
}
.App {
  text-align: center;
}

.TimeDisplay {
    position: relative;
}

.TimeDisplay svg {
    max-width: 300px;
}

.TimeDisplay circle {
    -webkit-transition: stroke-dashoffset 1s;
    transition: stroke-dashoffset 1s;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}

.TimeDisplay>div {
    position: absolute;
    left: 50%;
    top: 50%;
    display: grid;
    align-content: center;
    -webkit-transform: translate(-50%, -32px);
            transform: translate(-50%, -32px);
    text-align: center;
}

.TimeDisplay h1 {
    font-size: 52px;
    font-weight: 300;
    color: #D9534F;
    letter-spacing: 2px;
    margin: 0;
}

.TimeDisplay p {
    font-size: 14px;
    text-transform: uppercase;
    color: #bbb;
    letter-spacing: 3px;
    margin: 10px 0 0;
}
